import React from 'react';

const AddChar = ({ onSubmitChar }) => {
    let input;

    return (
        <div className="AddChar">
            <form
                onSubmit={e => {
                    e.preventDefault();
                    if (input.value.trim()) {
                        onSubmitChar(input.value);
                        input.value = '';
                    }
                }}
            >
                <input ref={node => input = node} placeholder="Enter the name" />
                <button type="submit">
                    Add to the list
        </button>
            </form>
        </div>
    );
};

export default AddChar;


