import React from 'react';
import PropTypes from 'prop-types';

const Char = ({ updateChar, deleteChar, name, status }) => (
    <li className={status}>
        <span onClick={updateChar}>{name}</span>
        <span onClick={deleteChar}>x</span>
    </li>
);

Char.propTypes = {
    updateChar: PropTypes.func.isRequired,
    deleteChar: PropTypes.func.isRequired,
    name: PropTypes.string.isRequired,
    status: PropTypes.string.isRequired
};

export default Char;


