import React from 'react';
import PropTypes from 'prop-types';
import Char from './Char';

const CharList = ({ chars, onUpdateChar, onDeleteChar }) => (
    <ul className="CharList">
        {chars.map(char =>
            <Char
                key={char.id}
                {...char}
                updateChar={() => onUpdateChar(char.id)}
                deleteChar={() => onDeleteChar(char.id)}
            />
        )}
    </ul>
);

CharList.propTypes = {
    chars: PropTypes.arrayOf(
        PropTypes.shape({
            status: PropTypes.string.isRequired,
            name: PropTypes.string.isRequired
        }).isRequired
    ).isRequired,
    onUpdateChar: PropTypes.func.isRequired,
    onDeleteChar: PropTypes.func.isRequired
};

export default CharList;


