import { connect } from 'react-redux';
import CharList from '../components/CharList';

const getVisibleChars = (chars, filter) => {
    switch (filter) {
        case 'SHOW_DEAD':
            return chars.filter(t => t.status === 'dead');
        case 'SHOW_ALIVE':
            return chars.filter(t => t.status === 'alive');
        case 'SHOW_ALL':
        default:
            return chars;
    }
};

const mapStateToProps = state => ({
    chars: getVisibleChars(state.chars, state.visibilityFilter)
});

export default connect(
    mapStateToProps
)(CharList);


