export const getChars = chars=> ({
    type: 'GET_CHARS',
    chars
})

export const addChar = char => ({
    type: 'ADD_CHAR',
    char
});

export const updateChar = char => ({
    type: 'UPDATE_CHAR',
    char
});

export const deleteChar = id => ({
    type: 'DELETE_CHAR',
    id
});

export const setVisibilityFilter = filter => ({
    type: 'SET_VISIBILITY_FILTER',
    filter
});

export const VisibilityFilters = {
    SHOW_ALL: 'SHOW_ALL',
    SHOW_ALIVE: 'SHOW_ALIVE',
    SHOW_DEAD: 'SHOW_DEAD'
};


