import React, {Component} from 'react';
import { connect } from 'react-redux';
import Header from './Header';
import AddChar from '../containers/AddChar';
import VisibleCharsList from '../containers/VisibleCharsList';
import FooterChar from './FooterChar';
import Footer from './Footer';
import { addChar, getChars, updateChar, deleteChar } from '../actions';

class App extends Component {
    constructor(props) {
        super(props);

        this.onSubmitChar = this.onSubmitChar.bind(this);
        this.onUpdateChar = this.onUpdateChar.bind(this);
        this.onDeleteChar = this.onDeleteChar.bind(this);
    }

    componentDidMount() {
        fetch("http://localhost:8080/chars")
            .then(response => response.json())
            .then(data => this.props.dispatch(getChars(data)));
    }

    onSubmitChar(nameChar) {
        const {dispatch} = this.props;
        const newChar = {
            name: nameChar,
            status: 'alive'
        };
        
        fetch("http://localhost:8080/chars", {
            method: 'POST',
            headers: new Headers({
                'Content-Type': 'application/json'
            }),
            body: JSON.stringify(newChar)
            }).then(res => res.json()).then(data => dispatch(addChar({ id: data, ...newChar})));
    }

    onUpdateChar(idChar) {
        const { dispatch } = this.props;
        fetch(`http://localhost:8080/chars/${idChar}`, {
            method: 'PUT',
            headers: new Headers({
                'Content-Type': 'application/json'
            }),
        }).then(res => res.json()).then(data => dispatch(updateChar(data)));
    }

    onDeleteChar(idChar) {
        const { dispatch } = this.props;
        fetch(`http://localhost:8080/chars/${idChar}`, {
            method: 'DELETE',
            headers: new Headers({
                'Content-Type': 'application/json'
            }),
        }).then(data => dispatch(deleteChar(idChar)));
    }

    render() {
        return (
            <div className="App">
                <Header />
                <div className="content">
                    <AddChar onSubmitChar={this.onSubmitChar}/>
                    <VisibleCharsList onUpdateChar={this.onUpdateChar} onDeleteChar={this.onDeleteChar}/>
                    <FooterChar />
                </div>
                <Footer />
            </div>
        )
    }
}

export default connect()(App);



