const chars = (state = [], action) => {
    switch (action.type) {
        case 'GET_CHARS': 
            return [...action.chars];
        case 'ADD_CHAR':
            return [
                ...state,
                {
                    id: action.char.id,
                    name: action.char.name,
                    status: action.char.status,
                }
            ];
        case 'UPDATE_CHAR':
            return state.map(char =>
                (char.id === parseInt(action.char.id, 10))
                    ? { ...char, status: action.char.status }
                    : char
            );
        case 'DELETE_CHAR':
            return state.filter(char => char.id !== parseInt(action.id, 10));
        default:
            return state;
    }
};

export default chars;


